<template>
  <div>
    <label class="btn-tool" :title="config.title" for="ithit-button-input">
      <i class="icon" :class="config.iconClassName"></i>
      <span v-html="config.innerHtml"></span
    ></label>
    <input
      id="ithit-button-input"
      ref="ithitHiddenInput"
      class="d-none"
      type="file"
      multiple
      hidden
    />
  </div>
</template>
<script>
import UploadService from "@/services/UploadService.js";
export default {
  props: {
    config: {
      type: Object,
      required: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      UploadService.addInput("ithit-button-input");
    });
  }
};
</script>
